.burger-menu {
    margin: 0 auto;
    width: 70px;
    display: block;
    transition: all .3s;
    cursor: pointer;
    height: 22px;
    .bar {
        transition: all .3s;
      height: 2px;
      width: 100%;
      display: block;
      background-color: white;
    }
      
    .bar:nth-of-type(2) {
        margin: 8px 0 0;
    }
      
    &--closed {
        transition-delay: .3s;
        .bar {
            float: right;
        }
        .bar:nth-of-type(2) {
            width: 75%;     
            transition-property: margin, height, width;
            transition-delay: .3s, .3s, 0s;
        }  
        &:hover {
            .bar:nth-of-type(2) {
              width: 100%;
            }
            }
        }
         
    &--opened {
        padding-top: 12px;
        .bar:nth-of-type(1) {
          transform: rotate(45deg);
          transition-delay: .3s;
          height: 2px;
        }
        .bar:nth-of-type(2) {
            transform: rotate(-45deg);
            transition-delay: .3s;
            height: 2px;
        }
        }
}
    